<template>
    <div ref="trys" :style="{height: showInfo ? 'calc(100% - 1.6em)' : '100%'}">
        <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" v-if="showInfo"></info>
        </div>
        <div v-show="!showInfo" class="all-container">
          <div class="operate-div" ref="operateDiv">
            <template v-for="(item, $index) in FormOpt">
                <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs> 
               <!-- <selects ref="selects" style="margin-left:10px" v-if="item.type == 'select'" :option="item.options"
                         @getSelect="getFrom"></selects>-->
                <datePicker ref="timer" style="margin-left:10px" v-if="item.type == 'timer'"     type= "doubleDate"  :dateOpt="item.options"
                       @getDatePicker="getFrom"></datePicker>
            </template>
          </div>
         <!--  <div class="deepFilter" ref="deepFilter" v-if="deepSearchData.length > 0">
            <label>筛选条件：</label>
            <template v-for="(data, $index) in deepSearchData">
            <span class="deepSearchSpan" :style="{'padding-right': data.flag ?  '1.5em' : '0.5em'}" >{{data.label}}： {{data.value}}
              <i v-if="data.flag" @click=" removeSearchData($index)">
                <icon name="remove" scale="0.8"></icon>
              </i>
            </span>
            </template>
          </div> -->
          <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 5em)'}">
            <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding" v-if="tableShow"
                    @rowDbClick="clickTableRow"></tables>
          </div>
        </div>
        <openCard ref="openCards"  @refreshAgain="getParams()"></openCard>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="loading" ref="loadings"></dialogs>
      <dialogs type="confirm" ref="confirms"></dialogs>
      <dialogs type="customer" ref="cancelPay" width="35em">
        <div slot="header">
          撤销记录
        </div>
        <div slot="content">
           <ul class="dialog-ul form-list">
             <li :class="item.classes" v-for="(item, $index) in dialogFrom" :ref="item.options.key" style="width:100%">
               <span class="label-show"><span class="emptyTips"> * </span>{{item.label}}</span>
               <inputs :option="item.options" @getInput="getFrom"></inputs>
             </li>
           </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="submitData">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
        </div>
      </dialogs>
        <!--</div>-->
    </div>
</template>
<script>
    import openCard from '../../childComponent/OpenAccount.vue'
    import info from './Info.vue'
    export default {
        data() {
            return {
                tableShow: false,
                loginAgain: {
                  password: '',
                  reasons: ''
                },
                tableHeight: '',
                clickEvent: true, //禁止重复提交数据
                row: '', //选中的行数据。。向详情传递
                showInfo: false, //详情延迟加载
                controls: [],
                deepSearchData: '',
                searchParam: {
                  startDate: '',
                  endDate: '',
                  writeMeter: '',
                  payModel: '',
                 // meterUserNo: '',
                //  deviceNo: '',
                  customerName: '',
                  customerContact: '',
                  customerAddr: '',
                  deviceType: '07',
                  payMethod: ''
                },
                searchType: 'meterUserNo',
                payDate: '',
                TableOpt: {
                    column: [
                        {title: '记录状态', field: 'isValid', textAlign: 'center', width: '120',   type:"string", formatter: function (val) {
                            if(val === 0){
                                return '<span class="red">已撤销</span>';}
                            else{
                                return '<span>正常</span>';}
                        }},
                        {
                            title: '充值状态',
                            field: 'writeMeter',
                            textAlign: 'center',
                            width: '110',
                            type:"string",
                            formatter: function (val, index, row, obj) {
                               if(val == 0) {
                                  if(row.payModel == 1) {
                                    return '正在远程充值'
                                  } 
                                  return '制卡失败'
                               } 
                               return obj.$parent.getEnumItem('chargeState', val).text;   
                            }
                        },
                    /*    {
                            title: '购电方式',
                            field: 'payModel',
                            textAlign: 'center',
                            width: '100',
                            type:"string",
                            formatter: function (val, index, row, obj) {
                              return obj.$parent.getEnumItem('chargModel', val).text;
                            }
                        },*/
                         {title: '流水号', field: 'serialNum', textAlign: 'center', width: '180'},
                        {title: '用户姓名', field: 'customerName', textAlign: 'center', width: '160'},
                        {title: '门牌编号', field: 'propertyName', textAlign: 'center',  width: '160'},
                       // {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '160'},
                        {
                          title: '缴费方式', 
                          field: 'payMethod', 
                          textAlign: 'center', 
                          width: '120',
                          type: 'string',
                          formatter: function(val) {
                            switch(val) {
                              case '1': 
                              case 1: 
                                return '现金';
                              case '2': 
                              case 2: 
                                return '微信';   
                              case '3': 
                              case 3: 
                                return '银联';
                              case '4': 
                              case 4: 
                                return '支付宝';  
                              default:
                                return '--'  
                            }
                          }
                        },
                        {title: '缴费金额', field: 'payment', textAlign: 'center', width: '120'},
                        {title: '调整金额', field: 'adjustAmount', textAlign: 'center', width: '120'},
                        {title: '充值金额', field: 'payMoney', textAlign: 'center', width: '120'},
                        {title: '账户余额', field: 'remainAmount', textAlign: 'center', width: '120'},
                      /*  {
                        title: '补卡',
                        field: '',
                        textAlign: 'center',
                        width: '80',
                        type:"object",
                        formatter: function (val, index, row, obj) {
                          //obj.$parent.controls[15]
                          if (row.isValid === 1 && row.isRepeatCard === 1 && row.isEnabled === 1 && obj.$parent.controls[15] && row.isChange == 0) {
                            let template;
                            template = '<button class="list-operate"  @click="openAccount">' +
                              '  补售电卡</button>';
                            return {
                              template: template,
                              methods: {
                                openAccount: function () {
                                  obj.$parent.openCard(row, 2);
                                }
                              }
                            }
                          }
                          else {
                            return {
                              template: "<div style='line-height: 2.3em;'>--</div>"
                            }
                          }
                        }
                      },
                        {
                        title: '撤销记录',
                        field: '',
                        textAlign: 'center',
                        width: '110',
                        type:"object",
                        formatter: function (val, index, row,obj) {
                          //obj.$parent.controls[18] &&
                          if (obj.$parent.controls[18] && row.isRepeatCard === 1 && row.isValid === 1 && row.isEnabled === 1) {
                            let template;
                            template = '<button class="list-operate"  @click="cancelRecharge()">' +
                              '撤销记录</button>';
                            return {
                              template: template,
                              methods: {
                                cancelRecharge: function () {
                                  obj.$parent.cancelPayment(row);
                                }
                              }
                            }
                          }
                          else {
                            return {
                              template: "<div  style=' line-height: 2.3em;'>--</div>"
                            }
                          }
                        }
                        }, */
                        {title: '购电日期', field: 'payDate', textAlign: 'center', width: '180'},
                        {title: '购电次数', field: 'payCount', textAlign: 'center', width: '120'},
                        {title: '联系电话', field: 'customerContact', textAlign: 'center', width: '140'},
                        {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '220'},
                        {title: '部门机构', field: 'orgName', textAlign: 'center', width: '130'},
                        {title: '售电员', field: 'createUserName', textAlign: 'center', width: '120'}
                    ],
                    url: 'pay/queryList',
                    method: 'post',
                   // url: 'dist/json/payment.json',
                   // method: 'get',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    queryParam: {deviceType: '07'},
                    showTotal: true,
                    isExport: false,
                    exportURL: 'pay/generateExcel',
                    dataRow: 'list',
                    totalOpt: [
                        {title: '缴费金额', field: 'payment', textAlign: 'center', width: '130'},
                        {title: '调整金额', field: 'adjustAmount', textAlign: 'center', width: '100'},
                         {title: '写表金额', field: 'payMoney', textAlign: 'center', width: '100'}]
                },
                select: ''
            }
        },
        components: {
            openCard,
            info
        },
        computed: {
            alerts: function () {
                return this.$refs.alerts
            },
            loadings: function () {
                return this.$refs.loadings
            },
            FormOpt: function () {
              let dates = this.initDate(true);
                return [
                  {
                    type: 'select',
                    options: {
                      key: 'searchParam.payMethod',
                      cs: 'client',
                      width: '11em',
                      data: [
                         {text: '全部方式', value: ''},
                         {text: '现金', value: 1},
                         {text: '微信', value: 2},
                         {text: '银联', value: 3},
                         {text: '支付宝', value: 4},
                      ]
                    }
                  },
                  {
                    type: 'select',
                    options: {
                      key: 'searchParam.writeMeter',
                      cs: 'client',
                      width: '11em',
                      data: this.getEnumItem('writeMeter').obj
                    }
                  },
                  {
                    type: 'select',
                    options: {
                      key: 'searchParam.payModel',
                      cs: 'client',
                      width: '9em',
                      value: 'val',
                      data: this.getEnumItem('chargModel').obj
                    }
                  },
                  {
                    type: 'timer',
                    options: {
                      key: 'payDate',
                      value: {startDate: dates.start, endDate: dates.end},
                      width: '23em',
                      fontSize: '0.8em'
                    }
                  },
                  {
                    type: 'input',
                    options: {
                      type: 'typeSearch',
                      key: 'searchParam',
                      selectOpt: {
                        width: '7em',
                        cs: 'client',
                        data: [
                         // {text: '表计户号', value: 'meterUserNo'},
                         // {text: '用户表号', value: 'deviceNo'},
                          {text: '用户姓名', value: 'customerName'},
                          {text: '联系电话', value: 'customerContact'},
                          {text: '门牌编号', value: 'propertyName'},
                          {text: '用户地址', value: 'customerAddr'}],
                        text: 'text',
                        value: 'value',
                        key: 'searchType'
                      }
                    }
                  }]
            },
            dialogFrom: function () {
                let width = 'calc(100% - 7em)';
               return  [
                 {
                   type: 'input',
                   label: '操作口令',
                   options: {
                     type: 'passwordInput',
                     width: width,
                     key: 'loginAgain.password',
                     placeHolder: '请输入登录密码',
                     value: this.loginAgain.password
                   }
                 },
                 {
                   label: '操作原因',
                   type: 'input',
                   options: {
                     maxLength : 100,
                     type: 'textArea',
                     width: width,
                     key: 'loginAgain.reasons',
                     placeHolder: '请输入撤销原因',
                     value: this.loginAgain.reasons
                   }
                 }
               ]
            }
        },
        methods: {
            //撤销记录之前的身份验证
          submitData: function () {
              let that = this;
              that.regularTest({
                emptyKey: ['loginAgain.password', 'loginAgain.reasons'],
                callback: function () {
                    that.getRequest({
                      url: 'user/validate',
                      method: 'post',
                       // url: 'dist/json/loginAgain.json',
                       // method: 'get',
                       param: {password: that.loginAgain.password},
                       success: function () {
                         let p ={payGuid: that.select.payGuid, reason: that.loginAgain.reasons, cNo: that.select.cno, customerNo:  that.select.customerNo};
                         that.cancelDialog();
                         that.$refs.confirms.show({
                           msg: '确定要撤销该充值记录？',
                           action: {
                             confirm: function () {
                               that.getRequest({
                                 url: 'pay/disablePay',
                                 method: 'post',
                                 param: p,
                                 success: function (data) {
                                  // that.getParams(1);
                                   that.closeDiv();
                                   that.alerts.show({
                                     msg: data.message
                                   })
                                 },
                                 error: function (data) {
                                   that.alerts.show({
                                     msg: data.message
                                   })
                                 }
                               }, true)
                             }
                           }
                         })
                       },
                      error: function (data) {
                           that.alerts.show({
                             msg: data.message
                           })
                      }
                    }, true);
                }
              })
          },
          cancelDialog: function () {
                this.$refs.cancelPay.close();
                this.loginAgain = this.cleanSearchData(this.loginAgain);
                this.clickEvent = true;
          },
          getFrom: function (val) {
            if (val.combine) {
              if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'startDate', 'endDate', 'payModel', 'writeMeter']);
                this.getOption(val, true);
                this.getParams(1);
              }
            } else {
              this.getOption(val, true);
            }
          },
            //返回到列表
            closeDiv: function () {
                this.showInfo = false;
                this.getParams();
            },
            //双击查看详情
            clickTableRow: function (val) {
                this.row = val.val;
                this.showInfo = true;
            },
            nextOperate: function () {

            },
            //撤销缴费记录
            cancelPayment: function (row) {
                if (this.clickEvent) {
                    this.clickEvent = false;
                    this.select = row;
                    this.$refs.cancelPay.show()
                }

            },
            //operate: 0: 开户， 1：重新写卡， 2：补售电卡， 3： 二次开户
            //param: {customerNo, cNo, payGuid}
            openCard: function (row, sign) {
                    this.selected = row;
                    this.$refs.openCards.show({
                        customerNo: row.customerNo,
                        payGuid:  row.payGuid,
                        cno: row.cno
                    }, sign);
            },
            removeSearchData: function (index) {
                let key = this.deepSearchData[index].key;
                if (key === 'payModel') {
                    this.$refs['selects'][1].constant = {text: '全部', value: ''};
                    this.searchParam.payModel = '';
                } else if (key === 'writeMeter') {
                    this.$refs['selects'][0].constant = {text: '全部状态', value: ''};
                    this.searchParam.writeMeter = '';
                } else {
                   this.searchParam[key] = this.$refs['inputs'][0].search = '';
                   this.getParams(1);
                }
            },
            //刷新表格数据
            getParams: function (pageNumber) {
              this.deepSearchData = [];
              let arr = this.getSearchData(this.searchParam);

              for (let key in arr) {
                let labelName = '';
                let value = arr[key];
                let removeFlag = true;
                if(key !== 'deviceType') {
                  switch (key) {
                    case 'payModel':
                      labelName = '充值方式';
                      value = this.getEnumItem('chargModel', value).text;
                      break;
                    case 'writeMeter':
                      labelName = '充值状态';
                      value = this.getEnumItem('writeMeter', value).text;
                      break;
                    case 'meterUserNo':
                      labelName = '表计户号';
                      break;
                    case 'deviceNo':
                      labelName = '用户表号';
                      break;
                    case 'customerName':
                      labelName = '用户姓名';
                      break;
                    case 'customerContact':
                      labelName = '联系电话';
                      break;
                    case 'customerAddr':
                      labelName = '用户地址';
                      break;
                    case 'startDate':
                        removeFlag = false;
                      labelName = '开始时间';
                      break;
                    case 'endDate':
                         removeFlag = false;
                      labelName = '结束时间';
                      break;
                  }
                  this.deepSearchData.push({label: labelName, value: value, key: key, flag: removeFlag});
                }
              }
              this.getHeight('calc(100% - 5em)');
              this.searchTable(arr, pageNumber);
            },
            init: function () {
                let that = this;
                let data = that.initDate(true);
                that.TableOpt.queryParam['startDate'] =  that.searchParam.startDate = data.start;
                that.TableOpt.queryParam['endDate'] =  that.searchParam.endDate =  data.end;
                that.TableOpt.isExport = that.controls[6];
                that.getParams(1);
                that.tableShow = true;
                /*window.onresize = function () {
                  that.windowChange();
                }*/
            },
          windowChange: function () {
            let that = this;
            this.getHeight('calc(100% - 5em)');
             /* window.onresize = function () {
                that.getHeight('calc(100% - 5em)');
              }*/
            }
        },
        mounted: function () {
          this.isIn();
          this.getAction();
        },
        watch: {
            'searchParam.payMethod': {
              handler() {
                this.getParams(1)
              }
            },
            'searchParam.payModel': {
              handler() {
                this.getParams(1)
              }
            },
           'searchParam.writeMeter': {
              handler() {
                this.getParams(1)
              }
            },
            payDate: {
              handler(val, oldVal) {
                if (val !== '' && val !== oldVal) {
                  let arr = val.split('至');
                  this.searchParam.startDate = arr[0];
                  this.searchParam.endDate = arr[1];
                  this.getParams(1)
                }
              }
            },
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";

</style>



